.ant-drawer-body {
  height: 100%;
  .drawer-container {
    height: 100%;
    .house-type-display {
      height: 40%;
      .house-image {
        margin-top: 20px;
        height: calc(100% - 90px);
        background-color: #fcfcfc;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        &.type-1 {
          background-size: 80%;
          background-image: url(~assets/images/house_type_1.jpeg);
        }
        &.type-2 {
          background-size: 80%;
          background-image: url(~assets/images/house_type_2.jpeg);
        }
        &.type-3 {
          background-image: url(~assets/images/house_type_3.jpeg);
        }
      }
    }
    .house-type-data {
      height: 60%;
    }
  }
}
